    -webkit-transition:.4s;
    transition:.4s;
    }
    .slider:before{
    position:absolute;
    content:"";
    height:15px;
    width:15px;
    left:0px;
    bottom:0px;
    background-color:white;
    -webkit-transition:.4s;
    transition:.4s;
    }
